@import "../../css/colors.scss";
* {
  // 배경
  .main {
    .clearBtn {
      position: absolute;
      top: 3px;
      right: 38px;
      color: $mainS;
      background-color: transparent;
      .faEraser {
        font-size: 1.5em;
        padding: 4px 0 4px 10px;
      }
    }
    .bg {
      padding-bottom: 180px;
      width: 100%;
      padding-top: 0;
      // 2023년에 읽을 도서
      .msg {
        color: $subB;
        font-weight: bold;
        font-size: 20px;
      }
    }

    // 첵 등록 input
    .registration {
      display: none;
      position: relative;
      margin-bottom: 50px;
      color: $mainT;
      input {
        width: 300px;
        border: 2px solid $mainT;
        border-radius: 6px;
        padding: 10px;
      }
      .clearBtn {
        position: absolute;
        top: 2px;
        right: 40px;
        color: $mainS;
        background-color: transparent;
        .faEraser {
          font-size: 1.3em;
          padding: 4px 0 4px 10px;
        }
      }
      .inputCloseBtn {
        color: $mainT;
        background-color: transparent;
        .faPlus {
          font-size: 1.5em;
        }
      }
    }
    .registration.on {
      display: block;
      display: flex;
      align-items: center;
    }
    // category
    .category {
      flex-wrap: wrap;
      width: 70%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 50px;

      .menu {
        display: flex;
        gap: 20px;
        font-size: 1.2em;
        color: $mainS;
        padding: 10px 0;
        li {
          cursor: pointer;
          padding: 10px 0;
        }
        .focused {
          color: $mainT;
          border-bottom: 2px solid $mainT;
        }
      }
    }
  }
}
