@import "../css/colors.scss";
* {
  // 배경

  // 책 모든 콘텐츠
  .flex-row {
    display: flex;
    justify-content: center;

    .bucket {
      padding: 0 10px;
      position: relative;
      width: 160px;
      height: 160px;
      margin: 8px;
      margin-bottom: 20px;
      /* 내용 글씨관련 */
      font-weight: bold;

      text-align: center;
      word-break: break-all;
      z-index: 0;
      // 즐겨찾기 아이콘
      .fastar {
        position: absolute;
        font-size: 2em;
        top: 0;
        left: 0;
        transform: translate(-50%, 0);
        color: $mainT;
        z-index: 2;
      }
      // 책 제목
      h4 {
        position: absolute;
        color: #ff4000;
        font-size: 20px;
        line-height: 1.5;
        z-index: 999;
      }
    }
    .bucket:hover {
      width: 180px;
      height: 180px;
    }
    //책을 읽었을 때 붙이는 스탬프
    .bucket.done::after {
      content: "";
      position: absolute;
      width: 180px;
      height: 180px;
      background-image: url("../img/stamp.png");
      background-size: cover;
      background-position: center;
      opacity: 0.4;
    }
    .img1 {
      background-image: url("../img/bucket-red.png");
      background-size: cover;
      background-position: center;
    }
    .img2 {
      background-image: url("../img/bucket-lightred.png");
      background-size: cover;
      background-position: center;
      color: $mainT;
    }
    .img2.on {
      filter: drop-shadow(3px 3px 5px $subG);
    }
    .center.oc {
      opacity: 50%;
      filter: grayscale(80%);
    }

    .content {
      display: flex;
      flex-direction: column;
      align-items: center;
      .finishWrite {
        display: none;
        background-color: $mainT;
        padding: 10px 15px 9px 15px;
        color: $subW;
        cursor: pointer;
        width: 50%;
      }
      .finishWrite.active {
        display: block;
      }
    }
  }
  .wrap {
    flex-wrap: wrap;
  }
}
