@import "../css/colors.scss";

// 도서 추가/삭제 버튼
.icon {
  display: flex;
  gap: 20px;
  font-size: 3em;
  color: $mainT;
  margin-top: 30px;
  margin-bottom: 50px;
  align-items: center;

  .circlePlus.on {
    color: $subG;
    pointer-events: none;
  }
  .squareMinus.on {
    color: $subG;
    pointer-events: none;
  }
  .stamp.on {
    color: $subG;
    pointer-events: none;
  }
}
