@import "../css/colors.scss";
.search {
  position: relative;
  display: flex;
  align-items: center;
  color: $mainT;
  .in {
    display: none;
    .clearBtn {
      right: 58px;
    }

    .searchInput {
      width: 300px;
      font-size: 1.2em;
      border: transparent;
      border-bottom: 2px solid $mainT;
      padding: 10px;
      background-color: transparent;
    }
  }
  .in.on {
    display: block;
  }

  .faMagnifyingGlass {
    font-size: 2em;
    margin-left: 20px;
  }
}
