@import "../../css/colors.scss";
.bookSearch {
  .bg {
    padding: 20px;
    height: 100%;
    .container {
      .title {
        position: relative;
        font-weight: bold;
        font-size: 1em;
        margin: 20px;
        .searchInput {
          width: 300px;
          font-size: 1.2em;
          border: transparent;
          border-bottom: 2px solid $mainT;
          padding: 10px;
          background-color: transparent;
        }
        .clearBtn {
          position: absolute;
          right: 0;
          color: $mainS;
        }
      }

      .flex-row {
        width: 80%;
        display: flex;
        justify-content: center;
        border: 2px solid $mainT;
      }
      .wrap {
        flex-wrap: wrap;
        .reference {
          padding: 40px 20px;
          display: flex;
          flex-direction: column;
          align-items: center;
          width: 200px;
          flex-grow: 1;
          text-align: center;
          word-break: keep-all;
          img {
            margin-bottom: 20px;
          }
          h2 {
            font-size: 1em;
            margin-bottom: 10px;
          }
          h6 {
            font-size: 0.8em;
            color: $mainT;
          }
        }
      }
    }
  }
}
