@import "../css/colors.scss";
// 첵 등록 input
.registration {
  display: none;
  position: relative;
  margin-bottom: 50px;
  color: $mainT;
  input {
    width: 300px;
    border: 2px solid $mainT;
    border-radius: 6px;
    padding: 10px;
  }
  .clearBtn {
    position: absolute;
    top: 3px;
    right: 38px;
    color: $mainS;
    background-color: $subW;
    .faEraser {
      font-size: 1.5em;
      padding: 4px 0 4px 10px;
    }
  }
  .inputCloseBtn {
    color: $mainT;
    background-color: transparent;
    .faPlus {
      font-size: 1.5em;
    }
  }
}
.registration.on {
  display: block;
  display: flex;
  align-items: center;
}
